<template>
  <div id="step-2" class="animate__animated animate__backInUp" v-loading="loading" element-loading-text="正在保存录入信息..." element-loading-background="rgba(243, 241, 235, 0.5)" element-loading-spinner="el-icon-loading">
    <br />
    <div style="
        color: #C7000B;
        font-size: 15px;
        border: 1px solid;
        border-radius: 15px;
        padding: 5px 10px;
        text-align: center;
      ">
      数字资产登记 ➡ 录入客户信息
    </div>
    <br />
    <v-form ref="form" v-model="valid" lazy-validation>
      <v-text-field append-icon="mdi-account" v-model="name" :counter="10" :rules="nameRules" label="姓名" value="test" required></v-text-field>
      <v-text-field append-icon="mdi-email" v-model="email" :rules="emailRules" label="电子邮件" required></v-text-field>
      <v-select v-model="select" :items="items" :rules="[(v) => !!v || 'Item is required']" label="性别" required></v-select>
      <v-text-field append-icon="mdi-av-timer" v-model="time" label="购买时间"></v-text-field>
      <v-text-field append-icon="mdi-town-hall" v-model="city" label="所在城市"></v-text-field>
      <v-checkbox v-model="checkbox" :rules="[(v) => !!v || '您必须同意《捷德 NFT 服务协议》条款!']" label="您同意《捷德 NFT 服务协议》条款" required></v-checkbox>
      <br />
      <v-btn class="white--text" block color="#C7000B" @click="nextStep">
        登记客户信息
      </v-btn>
    </v-form>
  </div>
</template>

<script>
export default {
  data: () => ({
    loading: false,
    valid: true,
    name: "Tom Hanks",
    nameRules: [
      (v) => !!v || "必须填写姓名",
      (v) => (v && v.length > 2) || "姓名长度必须大于 2 个汉字",
    ],
    email: "tom.hanks@outlook.com",
    emailRules: [
      (v) => !!v || "必须填写电子邮件地址",
      (v) => /.+@.+\..+/.test(v) || "电子邮件地址必须有效",
    ],
    select: "男性",
    items: ["男性", "女性"],
    checkbox: true,
    time: "2021 年 6 月 13 日",
    city: "四川省成都市"
  }),

  methods: {
    nextStep() {
      /* ============ */
      const ndef = new NDEFReader();
      ndef.scan().then(() => {
        ndef.onreadingerror = (event) => {
          console.log("onreadingerror");
        };
        ndef.onreading = (event) => {
          console.log("onreading");
        };
      }).catch(error => { });
      /* ============ */
      this.loading = true;
      setTimeout(() => {
        this.$router.push("/bind/step-4");
        this.loading = false;
      }, 2000);
    },
  },
};
</script>

<style lang="scss" scoped>
#step-2 {
  margin: auto 6vw;
}
</style>
